<template>
  <div class="admin">
    <div class="search">
      <el-row :gutter="10">
        <el-col :span="8" v-for="(item, i) in searchList" :key="i" class="col_item">
          <el-col :span="3" class="label">
            {{item.label}}
          </el-col>
          <el-col :span="18">
            <template v-if="item.type === 'input'">
              <el-input
          suffix-icon="el-icon-search" />
            </template>
            <template v-if="item.type === 'fromto'">
              <el-col :span="9">
                <el-input />
              </el-col>
              <el-col :span="6" style="text-align: center">~</el-col>
              <el-col :span="9">
                <el-input />
              </el-col>
            </template>
            <template v-if="item.type === 'select'">
              <el-select v-model="value" placeholder="请选择">
                <el-option
                  v-for="_item in item.options"
                  :key="_item.value"
                  :label="_item.label"
                  :value="_item.value">
                </el-option>
              </el-select>
            </template>
          </el-col>
        </el-col>
        <el-button type="primary" style="margin: 10px">search</el-button>
      </el-row>
      <div class="Border_Top_Box">
        <el-table
          :data="tableData"
          style="width: 100%">
          <el-table-column
            v-for="(item, i) in columns"
            :key="i"
            :prop="item.prop"
            :label="item.label"
            :width="item.width">
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>
<script>
const columns = [
  {
    prop: 'id',
    label: 'user id',
    width: '80'
  },
  {
    prop: 'email',
    label: 'user email',
    width: '150'
  },
  {
    prop: 'level',
    label: 'level',
    width: '50'
  },
  {
    prop: 'registration',
    label: 'registration time'
  },
  {
    prop: 'login',
    label: 'login time'
  },
  {
    prop: 'lastlogin',
    label: 'lastlogin time'
  },
  {
    prop: 'amount',
    label: 'amount',
    width: '80'
  },
  {
    prop: 'code',
    label: 'code',
    width: '60'
  },
  {
    prop: 'agent',
    label: 'agent',
    width: '60'
  },
  {
    prop: 'activate',
    label: 'activate'
  }
]

const tableData = Array(18).fill().map((item, i) => ({
  id: i,
  login: new Date().toLocaleString(),
  lastlogin: new Date().toLocaleString(),
  registration: new Date().toLocaleString(),
  email: '123456@qq.com',
  level: 2,
  amount: '123456',
  code: 99846,
  agent: 'PC',
  activate: '上海市普陀区金沙江路 1517 弄'
}))
export default {
  data () {
    const freeze = data => Object.freeze(data)
    return {
      value: '',
      searchList: freeze([
        {
          type: 'input',
          label: 'id'
        },
        {
          type: 'fromto',
          label: 'amount'
        },
        {
          type: 'input',
          label: 'code'
        },
        {
          type: 'input',
          label: 'email'
        },
        {
          type: 'fromto',
          label: 'Login Times'
        },
        {
          type: 'select',
          label: 'activate',
          options: [
            {
              label: 'a',
              value: 'a'
            },
            {
              label: 'b',
              value: 'b'
            }
          ]
        },
        {
          type: 'fromto',
          label: 'Registration Times'
        },
        {
          type: 'fromto',
          label: 'LastLogin Times'
        }
      ]),
      tableData: freeze(tableData),
      columns: freeze(columns)
    }
  }
}
</script>
<style lang="less" scoped>
.admin {
  background-color: #fff;
  min-height: 100vh;
  .search {
    padding: 10px;
  }
  .col_item {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 10px 0;
    .label {
      text-align: center;
      font-size: 14px;
    }
  }
}
</style>